@import "./src/sass/helpers/var";

.gallery {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 5% 10%;

    &__title {
        font-size: 2vw;
        display: flex;
        width: 100%;
        font-weight: 600;
        color: $black; }

    &__slider {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 2.5% 0;
        position: relative;

        &--wrapper {
            width: inherit;
            display: flex;
            justify-content: center;
            align-items: center; } }

    &__inner {
        display: flex;
        width: inherit;
        overflow: hidden;
        justify-content: space-between;
        align-items: center; }

    &__slide {
        padding: 2.5%;
        display: flex;
        justify-content: center;
        align-items: center; }

    &__arrows {
        width: 110%;
        top: 50%;
        left: 0;
        transform: translate(-5%, -50%);
        display: flex;
        z-index: 100;
        position: absolute;
        justify-content: space-between;
        align-items: center;

        &--prev,
        &--next {
            width: 5%;
            transform: scale(0.75);
            &:hover {
                cursor: pointer; } } } }

