@import './src/sass/helpers/var';

.product-description {
    width: 85%;
    display: flex;
    margin: 12.5% auto 7.5%;
    justify-content: space-between;
    align-items: flex-start;
    position: relative;

    &::before {
        content: '';
        display: block;
        position: absolute;
        right: 0;
        bottom: 0;
        height: 5vw;
        width: 5vw;
        transform: translate(50%, 50%);
        background: center / contain no-repeat url('../img/before_pattern.png'); }

    &--wrapper {
        display: flex;
        width: 55%;
        flex-direction: column;
        padding: 0; }

    &__title {
        font-size: 1.8vw;
        display: flex;
        width: 100%;
        font-weight: 700;
        padding-bottom: 2.5vw;
        color: $green; }

    &__legend {
        width: 100%;
        display: flex;
        font-size: 1vw;
        line-height: 1.5vw;
        text-align: justify; }

    &__caption {
        color: $green;
        font-weight: 600;
        font-size: 1.25vw; }

    &__img {
        margin-left: auto;
        width: 35%; } }

.list-properties {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 2.5% 0;
    // list-style-image: url('../img/before_list_smart.png')
    // list-style-position: inside

    &__item {
        font-size: 1vw;
        line-height: 1.5vw;
        position: relative;
        width: 100%;
        display: flex;
        height: 100%;
        margin: 0.25vw 0 0.25vw 10%;
        padding: 2.5% 5%;

        &::before {
            content: '';
            display: block;
            position: absolute;
            width: 10%;
            height: 75%;
            top: 50%;
            left: 0;
            transform: translate(-100%, -50%);
            background-image: url('../img/before_list_smart.png');
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain; } } }
