@import "./src/sass/helpers/var";

.logo {
  position: relative;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 15rem;
  transition: $transition;
  &__link {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    min-height: 12.5rem;
    position: relative;
    object-fit: cover;
    // background-color: $black
    transition: $transition;
    overflow: hidden;
    transform: translateY(-7.5%);

    border-radius: 50%; }

  &__icon {
    width: 15%;
    height: 15%;
    margin: 0 auto 0.25rem;
    transform: scale(1.1); }

  &__caption {
    transform: scale(1.1);

    display: flex;
    font-size: 2rem;
    color: $white;
    transition: $transition; } }

@keyframes showLogo {
  20% {
    background-color: $black; }
  100% {
    min-height: 15rem;
    background-color: $black; } }

@media screen and (max-width: 1224px) {
  .logo {
    &__link {
      &::after {
        min-height: 12.5rem; } } } }

@media screen and (max-width: 768px) {
  .logo {
    width: 15rem;
    &__link {
      min-height: 10rem;
      &::after {
        min-height: 10rem; } } } }

@media screen and (max-width: 480px) {
  .logo {
    width: 30%; } }
