html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow-x: hidden;
  text-transform: uppercase;
  color: #000000;
}

section {
  width: 100%;
}

a {
  text-decoration: none;
  text-align: center;
}

ul {
  list-style: none;
}

b {
  color: #439f35;
  font-weight: 600;
  margin-left: 0.25vw;
}

.img--wrapper {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.img--wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 5;
  background: #8e9eab;
  /* fallback for old browsers */
  background-image: url("../img/bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.link {
  display: flex;
  padding: 0.5rem 1rem;
  font-size: 1vw;
  justify-content: center;
  align-items: center;
  text-align: center;
  line-height: 1.5vw;
  transition: all 0.3s ease;
}

.link:hover {
  cursor: pointer;
}

.link--wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
}

.bold {
  font-weight: 600;
}

.breadcrumbs {
  display: flex;
  margin: 2.5% 0;
  font-size: 1.25vw;
}

.scroll--hidden {
  overflow-y: hidden;
  overflow-x: hidden;
}

.scroll-up {
  position: absolute;
  bottom: 0;
  right: 7.5%;
  background-color: #186C2C;
  width: 3.5vw;
  height: 2.5vw;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.scroll-up__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 50%;
  background-image: url("../img/sprite.svg#up-arrow");
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 10;
  background-position: center;
}

.fade {
  animation: fade 1s ease;
}

.show {
  display: flex;
}

.hide {
  display: none;
}

svg {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header {
  width: 100%;
  padding: 0 7.5%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 0 auto;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  transition: all 0.3s ease;
  overflow: hidden;
  animation: hideHeader 2s ease forwards;
}

.header img:nth-child(even) {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 75%;
  height: 225%;
  transform: translateY(65%);
}

.header--size {
  animation: showHeader 2s ease forwards;
}

.header--size .logo__link {
  transition: all 0.3s ease;
  transform: translateY(0);
}

.header--size .logo__caption {
  color: #000000;
  font-size: 1.5vw;
}

.header--position {
  animation: showHeader 2s ease forwards;
}

.header--position .logo__link {
  min-height: 10vh;
  transition: all 0.3s ease;
  transform: translateY(0);
}

.header--position .logo__caption {
  color: #000000;
  font-size: 1.5vw;
}

@keyframes showHeader {
  0% {
    transition: all 2s ease;
    clip-path: circle(15% at 15% 50%);
    background-color: #000000;
  }
  100% {
    clip-path: circle(200% at 15% 50%);
    background-color: #ffffff;
  }
}

@keyframes hideHeader {
  0% {
    transition: all 2s ease;
    background-color: #ffffff;
    clip-path: circle(200% at 15% 50%);
  }
  100% {
    clip-path: circle(7.5% at 12.5% 40%);
    background-color: #000000;
  }
}

@media screen and (max-width: 1366px) {
  .header {
    padding: 0 7.5%;
  }
}

@media screen and (max-width: 1224px) {
  .header {
    justify-content: space-between;
    transition: all 0.3s ease;
  }
  .header--direction {
    position: relative;
    transition: all 0.3s ease;
    background-color: #ccc61d;
    padding: 5% 0;
    flex-direction: column;
  }
  .header--direction .logo {
    width: 40%;
  }
}

@media screen and (max-width: 768px) {
  .header {
    min-height: 10vh;
  }
}

.navigation {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  align-items: center;
  position: relative;
  clip-path: circle(200% at 90%);
}

.navigation--align {
  align-items: baseline;
}

.navigation--wrapper {
  display: none;
}

.navigation--wrapper ul {
  display: flex;
}

.navigation__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 100%;
}

.navigation__item:hover > ul, .navigation__item:focus > ul, .navigation__item:active > ul {
  display: inherit;
}

.navigation__link {
  display: flex;
  width: 100%;
  justify-content: flex-start;
  align-items: center;
  color: #000000;
  font-size: 1vw;
  padding: 1vw 0.75vw;
  transition: all 0.3s ease;
}

.navigation__link:hover, .navigation__link:focus, .navigation__link:active {
  color: #439f35;
}

.navigation--show {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin: 0 0 0 auto;
  height: auto;
  width: 80%;
  padding: 2.5% 0;
}

.submenu {
  position: relative;
  display: none;
  flex-direction: column;
  top: 100%;
  left: 0;
}

.submenu__item {
  display: flex;
  width: 100%;
  position: relative;
  flex-direction: column;
}

.submenu__item:hover > ul, .submenu__item:focus > ul, .submenu__item:active > ul {
  display: inherit;
}

.submenu__link {
  display: flex;
  width: auto;
  color: #000000;
  font-size: 1vw;
  padding: 1vw 0.75vw;
  transition: all 0.3s ease;
  text-transform: uppercase;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;
}

.submenu__link:hover, .submenu__link:focus, .submenu__link:active {
  color: #439f35;
}

.menu {
  display: flex;
  position: relative;
  width: 3rem;
  height: 3rem;
  padding: 0.35rem 0.15rem;
  justify-content: center;
  align-items: center;
}

.menu:hover {
  cursor: pointer;
}

.menu span {
  margin: 0 auto;
  position: relative;
  width: 100%;
  height: 6px;
  background-color: #000000;
  display: block;
  transition: all 0.3s ease;
}

.menu span:before, .menu span:after {
  position: absolute;
  content: '';
  width: 100%;
  height: 6px;
  background-color: #000000;
  display: block;
  transition: all 0.3s ease;
}

.menu span:before {
  margin-top: -12px;
}

.menu span:after {
  margin-top: 12px;
}

.menu--toggle span {
  transition-duration: 0s;
  background-color: rgba(0, 0, 0, 0);
  transition-delay: 0.2s;
}

.menu--toggle span:before, .menu--toggle span:after {
  transition-property: margin, transform;
  transition-duration: 0.2s;
  transition-delay: 0.2s, 0s;
  margin-top: 0;
}

.menu--toggle span:before {
  transform: rotate(45deg);
}

.menu--toggle span:after {
  transform: rotate(-45deg);
}

.logo {
  position: relative;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 15rem;
  transition: all 0.3s ease;
}

.logo__link {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 12.5rem;
  position: relative;
  object-fit: cover;
  transition: all 0.3s ease;
  overflow: hidden;
  transform: translateY(-7.5%);
  border-radius: 50%;
}

.logo__icon {
  width: 15%;
  height: 15%;
  margin: 0 auto 0.25rem;
  transform: scale(1.1);
}

.logo__caption {
  transform: scale(1.1);
  display: flex;
  font-size: 2rem;
  color: #ffffff;
  transition: all 0.3s ease;
}

@keyframes showLogo {
  20% {
    background-color: #000000;
  }
  100% {
    min-height: 15rem;
    background-color: #000000;
  }
}

@media screen and (max-width: 1224px) {
  .logo__link::after {
    min-height: 12.5rem;
  }
}

@media screen and (max-width: 768px) {
  .logo {
    width: 15rem;
  }
  .logo__link {
    min-height: 10rem;
  }
  .logo__link::after {
    min-height: 10rem;
  }
}

@media screen and (max-width: 480px) {
  .logo {
    width: 30%;
  }
}

.chooseLanguage {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 2.5rem;
}

.chooseLanguage__item {
  display: flex;
  font-size: 1.1vw;
  border: none;
  background-color: transparent;
  position: relative;
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 0 0.5rem;
  transition: all 0.3s ease;
}

.chooseLanguage__item:hover, .chooseLanguage__item:focus, .chooseLanguage__item:active {
  cursor: pointer;
  color: #439f35;
}

.chooseLanguage__item:nth-child(2n)::after, .chooseLanguage__item:nth-child(2n)::before {
  content: '';
  display: block;
  position: absolute;
  background-color: #ffffff;
  width: 1px;
  height: 100%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.chooseLanguage__item:nth-child(2n)::before {
  left: 0;
}

.chooseLanguage__item:nth-child(2n)::after {
  right: 0;
}

@media screen and (max-width: 1224px) {
  .chooseLanguage__item {
    font-size: 1.25vw;
  }
}

@media screen and (max-width: 1024px) {
  .chooseLanguage__item {
    font-size: 1.5vw;
  }
}

@media screen and (max-width: 768px) {
  .chooseLanguage__item {
    font-size: 2vw;
  }
}

@media screen and (max-width: 480px) {
  .chooseLanguage__item {
    font-size: 4vw;
  }
}

.slider {
  position: relative;
  display: block;
  width: 100%;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-image: url("../img/slider_bg.jpg");
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.slider__nav-bar {
  position: absolute;
  width: auto;
  min-height: 5rem;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  background-color: transparent;
  z-index: 100;
  display: flex;
  justify-content: space-between;
}

.slider__arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 5rem;
  height: 5rem;
  transition: 0.2s;
  cursor: pointer;
  z-index: 100;
  opacity: 0.8;
}

.slider__arrow:hover {
  opacity: 1;
}

.slider__arrow_prev, .slider__arrow_next {
  width: 5rem;
  height: 5rem;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: auto 5rem;
}

.slider__arrow_prev {
  background-image: url("../img/sprite.svg#left");
  left: 1rem;
}

.slider__arrow_next {
  background-image: url("../img/sprite.svg#right");
  right: 1rem;
}

.slider--size {
  height: 100vh;
}

.nav-control {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  opacity: 0.5;
  width: 2.5rem;
  height: 2.5rem;
  transition: 0.2s;
  cursor: pointer;
  background-image: url("../img/dot-black.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 75% 75%;
  z-index: 3;
}

.nav-control:hover {
  opacity: 0.8;
}

.nav-control_active {
  opacity: 1;
}

.slider-list {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.slider-list::before {
  content: '';
  display: block;
  padding-top: calc(9 / 16 * 100%);
}

.slider-list__item {
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16vw;
  color: #000;
  will-change: transform;
  z-index: 1;
}

.slider-list__item .back__element {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.slider-list__item .back__element img {
  position: relative;
  display: block;
  width: 100%;
  max-width: 75%;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.slider-list__item .main__element {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 3;
}

.slider-list__item .main__element img {
  position: relative;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.slider-list__item .front__element {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 4;
}

.slider-list__item .front__element img {
  position: relative;
  display: block;
  width: 100%;
  max-width: 55%;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.slider-list__item .title__element {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
}

.slider-list__item .title__element .title {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  top: 15%;
  left: 70%;
  transform: translate(-50%, 0%);
  color: #eef2f3;
  text-align: center;
  font-size: 10vw;
}

.slider-list__item .more__element {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.slider-list__item .more__element .content {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  max-width: 75%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: auto;
  margin: 0 auto;
  padding: 5% 2rem;
  color: #2e3131;
  text-align: left;
}

.slider-list__item .more__element .content .headline {
  position: relative;
  display: block;
  max-width: 240px;
  font-family: 'Molle', cursive;
  font-size: 1vw;
  line-height: 1.5vw;
}

.slider-list__item .more__element .content .excerpt {
  display: flex;
  max-width: 20%;
  font-size: 1vw;
  line-height: 1.25vw;
  padding: 1rem 0;
}

.slider-list__item .more__element .content .link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  font-size: 1vw;
  line-height: 24px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.05), 0 10px 10px rgba(0, 0, 0, 0.06);
  transition: all 0.4s ease;
}

.slider-list__item .more__element .content .link:hover {
  box-shadow: 0 0 20px 2.5px #d43325;
}

.slider-list__item .more__element .content .link .fill {
  position: absolute;
  background-image: linear-gradient(to right, #d43325, #f36e67);
  background-size: 150% 150%;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  margin: 0;
  z-index: -1;
  transition: all 0.4s ease;
  -webkit-animation: gradient 3s ease infinite;
  -moz-animation: gradient 3s ease infinite;
  animation: gradient 3s ease infinite;
}

.slider-list__item .more__element .content .link .fill.fill-dark {
  background-image: linear-gradient(to right, #252228, #848f9e);
}

.slider-list__item .more__element .content .link a {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: #dde3e6;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  outline: 0px none;
  outline: 0px;
  font-size: 0.8vw;
  z-index: 2;
  top: 50%;
  left: 50%;
  transition: all 0.3s ease;
  transform: translate(-50%, -50%);
}

.slider-list__item .more__element .content .link:hover {
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.25), 0 15px 12px rgba(0, 0, 0, 0.06);
}

.slider-list__item .more__element .content .link:hover a {
  color: black;
}

.slider-list__item_active {
  -webkit-transform: translateX(0);
  transform: translateX(0);
  z-index: 2;
}

@media screen and (max-width: 1600px) {
  .slider-list__item .more__element .content .excerpt {
    max-width: 25%;
    font-size: 1.2vw;
  }
}

@media screen and (max-width: 1366px) {
  .slider-list__item .more__element .content {
    max-width: 90%;
  }
  .slider-list__item .more__element .content .link a {
    font-size: 1vw;
  }
}

@media screen and (max-width: 1024px) {
  .slider-list__item .more__element .content .excerpt {
    max-width: 35%;
    font-size: 1.5vw;
    line-height: 2vw;
  }
}

@media screen and (max-width: 768px) {
  .slider {
    height: 60vh;
  }
  .slider-list__item .more__element .content {
    padding: 10% 7.5%;
  }
  .slider-list__item .more__element .content .excerpt {
    max-width: 40%;
    font-size: 1.75vw;
    line-height: 2.25vw;
  }
  .slider-list__item .more__element .content .link {
    width: 7.5rem;
    height: 7.5rem;
  }
  .slider-list__item .more__element .content .link a {
    font-size: 1.75vw;
  }
}

@media screen and (max-width: 480px) {
  .slider {
    height: 100vh;
  }
  .slider__nav-bar {
    min-height: 3.5rem;
  }
  .slider__arrow_prev, .slider__arrow_next {
    width: 2.5rem;
    height: 2.5rem;
    background-size: auto 2.5rem;
  }
  .slider-list__item .more__element .content {
    max-width: 100%;
    flex-direction: column;
    padding: 15% 7.5%;
    align-items: center;
    justify-content: flex-end;
  }
  .slider-list__item .more__element .content .link {
    width: 7.5rem;
    height: 7.5rem;
  }
  .slider-list__item .more__element .content .link a {
    font-size: 3.5vw;
  }
  .slider-list__item .more__element .content .excerpt {
    max-width: 70%;
    font-size: 3.5vw;
    line-height: 5vw;
    text-align: center;
  }
  .slider-list__item .main__element img,
  .slider-list__item .back__element img,
  .slider-list__item .front__element img {
    height: 50%;
    transform: translate(-50%, 0);
  }
}

.about-us {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.about-us__img {
  position: relative;
  width: 30%;
  min-height: 75vh;
  overflow: initial;
}

.about-us__img::before {
  width: 100%;
  height: 100%;
  content: '';
  display: block;
  position: absolute;
  z-index: 2;
  background-color: #000000;
  transform: translate(15%, 10%);
}

.about-us__img img {
  position: absolute;
  z-index: 3;
}

.about-us__description {
  display: flex;
  flex-direction: column;
  padding: 5% 15%;
  width: 70%;
}

.about-us__text {
  font-size: 1.1vw;
  text-align: justify;
  padding: 2.5% 0;
  color: #000000;
  line-height: 1.5vw;
}

@media screen and (max-width: 1600px) {
  .about-us__description {
    padding: 5% 17.5%;
  }
}

@media screen and (max-width: 1224px) {
