.contact-us {
  display: flex;
  width: 100%;
  align-items: flex-end;
  justify-content: space-between;
  padding: 7.5% 7.5% 0 0;
}

.contact-us__wrapper {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-us__legend {
  background: url("../img/legend.png") center/cover no-repeat;
  padding: 12.5% 7.5% 12.5% 20%;
  display: flex;
  justify-content: center;
  text-align: right;
  align-items: center;
  font-size: 1vw;
  line-height: 1.75vw;
  color: #ffffff;
  font-weight: 600;
  position: relative;
  height: 50%;
  width: 100%;
}

.contact-us__legend::after {
  content: '';
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  width: 50%;
  height: 50%;
  background-color: #186C2C;
}

.form-contact {
  width: 100%;
  display: flex;
  padding: 0 5% 5%;
  justify-content: space-around;
  align-items: center;
}

.form-contact--wrapper {
  display: flex;
  flex-direction: column;
  width: 50%;
  justify-content: space-between;
}

.form-contact__legend {
  font-size: 1vw;
  padding: 1rem 0;
  color: #186C2C;
  font-weight: 600;
  text-align: center;
}

.form-contact__title {
  padding-top: 1.5vw;
  font-size: 1.5vw;
  color: #000000;
  display: flex;
  justify-content: center;
  font-weight: 600;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.form-contact__input {
  margin: 2.5rem 0 0;
  padding: 1rem 1.5rem;
  font-size: 1vw;
  line-height: 1.5vw;
  width: 100%;
  border-radius: 10px;
  border: 1px solid #186C2C;
  transition: all 0.3s ease;
}

.form-contact__input:hover {
  box-shadow: -10px 10px 15px 2.5px rgba(67, 159, 53, 0.15);
}

.form-contact__btn {
  display: flex;
  width: 10vw;
  height: 10vw;
  border: none;
  font-size: 1.25vw;
  color: #ffffff;
  background-color: #186C2C;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
  margin: 12.5% auto;
  z-index: 5;
  padding: 7.5%;
  white-space: inherit;
  text-transform: uppercase;
  transition: all 0.3s ease;
  position: relative;
  box-shadow: -10px 10px 15px 2.5px rgba(0, 0, 0, 0.15);
}

.form-contact__btn:hover {
  cursor: pointer;
  box-shadow: -10px 10px 15px 2.5px rgba(67, 159, 53, 0.15);
}

.form-contact__btn--wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
  height: 50%;
  position: relative;
  padding: 10% 2.5%;
}

.form-contact__btn--wrapper::after {
  content: '';
  display: block;
  position: absolute;
  top: 25%;
  left: 25%;
  height: 100%;
  width: 100%;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
  background-image: url("../img/form_submit-wrapper.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  animation: showPulse 4s ease-in forwards infinite;
  transform: scale(0.5) translate(-50%, -50%);
}

@media screen and (max-width: 1600px) {
  .contact-us__legend {
    font-size: 1vw;
  }
}

@media screen and (max-width: 1366px) {
  .form-contact__input {
    margin-top: 1.5rem;
  }
}

@media screen and (max-width: 1224px) {
  .contact-us {
    padding: 10% 7.5% 0 0;
  }
}

@media screen and (max-width: 1024px) {
  .form-contact {
    padding: 0 0 5% 5%;
  }
  .form-contact__btn--wrapper {
    width: 50%;
  }
}

.contacts {
  width: 85%;
  position: relative;
  display: flex;
  align-items: center;
  margin: 10% auto 5%;
}

.contacts--wrapper {
  display: grid;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 5rem;
}

.contacts__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  align-items: center;
  box-shadow: -10px 10px 15px 2.5px rgba(0, 0, 0, 0.15);
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  transition: all 0.3s ease;
  background-color: #ffffff;
}

.contacts__item:hover {
  box-shadow: -10px 10px 10px 2.5px rgba(24, 108, 44, 0.25);
}

.contacts__item--bg {
  background-color: #186C2C;
  background-image: url("../img/contacts_bg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.list-contacts {
  padding: 10% 15%;
}

.list-contacts__item {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.list-contacts__caption, .list-contacts__link, .list-contacts__description {
  padding: 0.35rem 0;
  text-align: left;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  font-size: 0.8vw;
  color: #000000;
}

.list-contacts__caption {
  font-weight: 600;
  color: #186C2C;
  font-size: 1vw;
}

.btn--wrapper {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: space-between;
}

.btn--download {
  display: flex;
  width: 10vw;
  height: 10vw;
  border: none;
  padding: 0 7.5%;
  white-space: inherit;
  background-color: transparent;
  font-size: 1.25vw;
  color: #ffffff;
  background: 80% top/25% no-repeat url("../img/pdf.png"), center/contain no-repeat url("../img/form_submit.png");
  z-index: 5;
  font-size: 0.8vw;
  text-transform: uppercase;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
  box-shadow: -10px 10px 10px 2.5px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease;
}

.btn--download:hover {
  cursor: pointer;
  box-shadow: -10px 10px 10px 2.5px rgba(24, 108, 44, 0.25);
}

.footer {
  width: 100%;
  display: flex;
  align-items: center;
  position: sticky;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
  padding: 5% 12.5%;
  background-position: no-repeat;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("../img/footer_bg.jpg");
}

.menu-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.menu-links__item {
  width: calc( 100% / 3);
  display: flex;
  min-height: 15rem;
  height: auto;
  padding: 1.5vw 2.5vw;
  justify-content: center;
  align-items: center;
  position: relative;
  line-height: 1.1vw;
  color: #000000;
}

.menu-links__item:nth-child(even) {
  margin: 0 2vw;
}

.menu-links__item::after {
  display: block;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border: solid 1px #186C2C;
}

.menu-links__item img {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #000000;
  z-index: 5;
}

.menu-links__item p {
  font-size: 0.8vw;
}

.phone-list {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-right: 0.25vw;
}

.phone-list h3 {
  font-size: 0.7vw;
  font-weight: 600;
}

.phone-list a {
  font-size: 0.8vw;
  color: #000000;
}

.phone-list a:hover {
  cursor: pointer;
  color: #186C2C;
}

@media screen and (max-width: 1600px) {
  .menu-links__item {
    padding: 2.5vw;
    min-height: 12.5rem;
  }
  .menu-links__item p {
    font-size: 0.8vw;
    line-height: 1.25vw;
  }
  .phone-list h3 {
    font-size: 0.8vw;
  }
  .phone-list a {
    font-size: 0.8vw;
