@keyframes hidePulse {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}

.products {
  width: 100%;
  min-height: 100vh;
}

.products--wrapper {
  width: 65%;
  margin: 10% auto;
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 1.5vw 2.5vw;
  justify-items: center;
  align-content: center;
}

.products__item {
  display: flex;
  flex-direction: column;
  width: 75%;
}

.product__img {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
  transition: all 0.3s ease;
  box-shadow: -10px 10px 10px 2.5px rgba(24, 108, 44, 0.25);
}

.product__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product__img .img--absolute {
  position: absolute;
  object-fit: contain;
  top: 0;
  left: 2.5%;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
  overflow: hidden;
}

.product__img::after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  transform: scale(0.9);
  animation: hidePulse 1s ease-in-out forwards;
  transition: all 0.3s ease;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
}

.product__img:hover {
  cursor: pointer;
  box-shadow: -10px 10px 20px 2.5px rgba(67, 159, 53, 0.5);
}

.product__img:hover::after {
  animation: showPulse 2.5s ease-in-out forwards infinite;
  transition: all 0.3s ease;
  opacity: 1;
  visibility: visible;
}

.product__title {
  display: flex;
  padding: 2.5% 0;
  font-size: 1.5vw;
  justify-content: center;
  align-items: center;
  font-weight: 600;
}

.about {
  display: flex;
  width: 100%;
  min-height: 100vh;
  background-image: url("../img/slider_bg.jpg");
  margin-top: 10%;
}

.history {
  margin-left: 35%;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  padding: 0 5%;
  background-image: url("../img/bg.jpg");
  background-attachment: fixed;
}

.history__title {
  font-size: 2vw;
  font-weight: 600;
  text-transform: initial;
  color: #000000;
}

.history--wrapper {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 2.5vw;
  padding: 5%;
  justify-content: center;
  align-items: center;
}

.event {
  background-color: #f1f1f1;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  padding: 10%;
  box-shadow: -10px 10px 15px 2.5px rgba(0, 0, 0, 0.15);
  height: 100%;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  transition: all 0.3s ease;
}

.event:hover {
  box-shadow: -10px 10px 15px 2.5px rgba(67, 159, 53, 0.15);
}

.event__number {
  text-align: center;
  font-size: 2vw;
  font-weight: 600;
  color: #186C2C;
}

.event__description {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 5% 0;
  line-height: 1vw;
  color: #000000;
}

.gallery {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 5% 10%;
}

.gallery__title {
  font-size: 2vw;
  display: flex;
  width: 100%;
  font-weight: 600;
  color: #000000;
}

.gallery__slider {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2.5% 0;
  position: relative;
}

.gallery__slider--wrapper {
  width: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gallery__inner {
  display: flex;
  width: inherit;
  overflow: hidden;
  justify-content: space-between;
  align-items: center;
}

.gallery__slide {
  padding: 2.5%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gallery__arrows {
  width: 110%;
  top: 50%;
  left: 0;
  transform: translate(-5%, -50%);
  display: flex;
  z-index: 100;
  position: absolute;
  justify-content: space-between;
  align-items: center;
}

.gallery__arrows--prev, .gallery__arrows--next {
  width: 5%;
  transform: scale(0.75);
}

.gallery__arrows--prev:hover, .gallery__arrows--next:hover {
  cursor: pointer;
}

.blog {
  width: 100%;
  min-height: 100vh;
  position: relative;
}

.blog__slider {
  width: 75%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 7.5% auto 0;
  padding-bottom: 2.5%;
}

.blog__slider--wrapper {
  width: 100%;
  justify-content: center;
  align-items: center;
}

.blog__inner {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.blog__arrows {
  width: 15%;
  bottom: 0;
  right: 0;
  left: 100%;
  top: 100%;
  transform: translate(-100%, 100%);
  display: flex;
  z-index: 100;
  position: absolute;
  justify-content: space-between;
  align-items: center;
}

.blog__arrows--prev, .blog__arrows--next {
  transform: scale(0.35);
}

.blog__arrows--prev:hover, .blog__arrows--next:hover {
  cursor: pointer;
}

.page-blog {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, auto);
  justify-items: space-between;
  align-content: center;
  padding: 2.5%;
  grid-gap: 5vw;
}

.page-blog__item {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10%;
  box-shadow: -10px 10px 15px 2.5px rgba(0, 0, 0, 0.15);
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  transition: all 0.3s ease;
  height: 100%;
  color: #000000;
  background-color: #ffffff;
  position: relative;
}

.page-blog__item::before {
  content: '';
  display: block;
  position: absolute;
  height: 3.5vw;
  width: 3.5vw;
  border-radius: 50%;
  bottom: 0;
  right: 0;
  transition: all 0.3s ease;
  transform: translate(50%, 50%);
  background-color: #ccc;
}

.page-blog__item::after {
  content: '';
  display: block;
  position: absolute;
  height: 1.75vw;
  width: 1.75vw;
  border-radius: 50%;
  bottom: 0;
  right: 0;
  transition: all 0.3s ease;
  transform: translate(50%, 50%);
  border: 5px solid #ffffff;
}

.page-blog__item:hover {
  background-color: #186C2C;
  color: #ffffff;
  box-shadow: -10px 10px 15px 2.5px rgba(67, 159, 53, 0.15);
  cursor: pointer;
}

.page-blog__item:hover::before {
  background-color: #ffffff;
}

.page-blog__item:hover::after {
  border: 5px solid #186C2C;
}

.page-blog__description {
  display: flex;
  justify-content: stretch;
  align-items: center;
  text-align: justify;
  line-height: 1.25vw;
}

.legend--width {
  width: calc( 100% / 3.5);
}

.location {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.location__map {
  width: 100%;
  height: 100%;
}

.contact-form {
  padding: 0 17.5%;
}

.contact-form .form-contact--wrapper {
  width: 100%;
}

.product-description {
