  display: flex;
  margin: 12.5% auto 7.5%;
  justify-content: space-between;
  align-items: flex-start;
  position: relative;
}

.product-description::before {
  content: '';
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  height: 5vw;
