  padding: 2.5% 5%;
  box-shadow: -10px 10px 15px 2.5px rgba(0, 0, 0, 0.15);
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 1.5vw;
  font-size: 1vw;
  color: #186C2C;
  text-align: justify;
  transition: all 0.3s ease;
  background-color: #ffffff;
}

.glue-page {
  flex-wrap: wrap;
}

.glue-page__description {
  background-color: #ffffff;
  display: flex;
  text-align: justify;
  font-size: 1vw;
  border: 2px solid #439f35;
  line-height: 1.5vw;
  padding: 2.5% 7.5%;
  margin: 7.5% 0;
  border-top-left-radius: 30px;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
}

.glue-page__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1.5vw;
}

.glue-page__list--wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-between;
}

.glue {
  display: flex;
  justify-content: space-between;
  width: 85%;
  margin: 2.5% auto;
}

.glue--reverse {
  flex-direction: row-reverse;
}

.glue--reverse .glue--wrapper {
  margin-left: 0;
  margin-right: 10%;
}

.glue--wrapper {
  display: flex;
  flex-direction: column;
  width: 60%;
  align-items: center;
  margin-left: 10%;
}

.glue__title {
  font-size: 1.5vw;
  display: flex;
  font-weight: 700;
  color: #186C2C;
  width: 100%;
  margin-bottom: 2.5vw;
}

.glue__img {
  position: relative;
  width: 100%;
  height: 27.5vw;
  background: url("../img/glue_bg.jpg") center/cover no-repeat;
}

.glue__img::before {
  content: '';
  position: absolute;
  display: block;
  bottom: 0;
  right: 0;
  height: 7.5vw;
  width: 7.5vw;
  background: center/contain no-repeat url("../img/after_pattern.png");
}

.glue-list {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.glue-list__item {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 1vw;
  position: relative;
}

.glue-list__radio {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}

.glue-list__radio:checked ~ .glue-list__title {
  color: #ffffff;
  background-color: #186C2C;
  box-shadow: -5px 5px 10px 5px rgba(67, 159, 53, 0.25);
}

.glue-list__radio:checked ~ .glue-list__title::after {
  background: url("../img/minus.png") center/contain no-repeat;
}

.glue-list__radio:checked ~ .glue-list__description {
  display: flex;
}

.glue-list__title {
  padding: 2.5% 5%;
  display: flex;
  align-items: center;
  background-color: #ffffff;
  font-size: 1vw;
  font-weight: 600;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  transition: all 0.5s ease-in-out;
  position: relative;
}

.glue-list__title:hover {
  color: #ffffff;
  background-color: #186C2C;
  box-shadow: -5px 5px 10px 5px rgba(67, 159, 53, 0.25);
  cursor: pointer;
}

.glue-list__title:hover::after {
  transition: all 0.3s ease;
  background: url("../img/plus_white.png") center/contain no-repeat;
}

.glue-list__title::after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  right: 2.5%;
  transform: translate(-5%, -50%);
  border-radius: 50%;
  width: 2vw;
  height: 2vw;
  transition: all 1s ease-in-out;
  background: url("../img/plus.png") center/contain no-repeat;
}

.glue-list__description {
  font-size: 1vw;
  display: flex;
  text-align: justify;
  padding: 2.5%;
  line-height: 1.5vw;
  display: none;
}

.btn--order {
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  width: 50%;
  margin: 15% auto;
  padding: 1vw 0.5vw;
  font-size: 1vw;
  border: none;
  text-transform: uppercase;
  font-weight: 600;
  color: #ffffff;
  background-color: #439f35;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
}

